:not(pre) > code {
  font-size: 1.3rem;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, monospace;
  background: #fff;
  border: 0.1rem solid #ebeced;
  padding: 0.1em 0.05em;
  margin: 0 0.1em;
}

@media screen and (min-width: 480px) {
  :not(pre) > code {
    font-size: 1.3rem;
  }
}

:not(pre) > code:before, :not(pre) > code:after {
  content: '\00a0';
  letter-spacing: -0.2em;
}

pre {
  font-size: 1.3rem;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, monospace;
  background: #fff;
  border: 0.1rem solid #ebeced;
  padding: 1em;
  margin: 1em 0;
  overflow: auto;
}

@media screen and (min-width: 480px) {
  pre {
    font-size: 1.3rem;
  }
}

pre > code {
  color: #32414d;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  tab-size: 2;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata,
.token.punctuation {
  color: #99a0a6;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #905;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #5e8e01;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #a67f59;
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #0070a3;
}

.token.function {
  color: #dd4a68;
}

.token.regex,
.token.important,
.token.variable {
  color: #e90;
}

::selection {
  background: #ffdd59;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  background: #fffef2;
  color: #32414d;
  font-family: neue-haas-unica, Helvetica, Arial, Roboto, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

@media screen and (min-width: 480px) {
  body {
    font-size: 1.6rem;
  }
}

h1 {
  font-size: 3.6rem;
  color: #000;
  line-height: 1.25;
  margin: 0.5em 0;
}

@media screen and (min-width: 480px) {
  h1 {
    font-size: 3.6rem;
  }
}

h2 {
  font-size: 2.4rem;
  color: #000;
  line-height: 1.25;
  margin: 2em 0 0.5em;
}

@media screen and (min-width: 480px) {
  h2 {
    font-size: 2.4rem;
  }
}

h3 {
  font-size: 2rem;
  color: #000;
  line-height: 1.25;
  margin: 2em 0 0.5em;
}

@media screen and (min-width: 480px) {
  h3 {
    font-size: 2rem;
  }
}

h4, h5, h6 {
  color: #000;
}

a {
  color: #32414d;
}

a:hover {
  color: #3867d6;
}

em,
i {
  font-style: italic;
}

ul,
ol {
  padding: 0 0 0 2em;
}

blockquote {
  color: #5b6771;
  border-left: 0.2em solid #32414d;
  margin: 2em 0;
  padding: 0 2em;
}

blockquote cite {
  font-style: normal;
}

table {
  font-size: 1.3rem;
  border-spacing: 0;
  border: 0.1rem solid #ebeced;
  width: 100%;
}

@media screen and (min-width: 480px) {
  table {
    font-size: 1.3rem;
  }
}

tr th,
tr td {
  background: #fff;
  text-align: left;
  vertical-align: top;
  padding: 0.5em 1em;
  border-bottom: 0.1rem solid #ebeced;
}

tr:last-child td {
  border-bottom: none;
}

img {
  height: auto;
  max-width: 100%;
}

hr {
  border: 0;
  height: 0;
  border-bottom: 0.1rem solid #ebeced;
}

.header {
  width: 100%;
  max-width: 56rem;
  padding: 0 2rem;
  margin: 0 auto;
  display: flex;
  align-items: center;
  height: 5em;
}

.header__logo a, .header__logo svg {
  display: block;
}

.title {
  font-family: orpheuspro, Georgia, serif;
  font-weight: 500;
}

.main {
  width: 100%;
  max-width: 56rem;
  padding: 0 2rem;
  margin: 0 auto;
  padding-top: 1em;
  padding-bottom: 6em;
}

.hero__links a {
  font-size: 1.6rem;
  color: #32414d;
  text-decoration: none;
  border-bottom: solid 0.2rem rgba(91, 103, 113, 0.5);
}

@media screen and (min-width: 480px) {
  .hero__links a {
    font-size: 1.6rem;
  }
}

.hero__links a:hover {
  text-decoration: none;
  border-bottom: solid 0.2rem #3867d6;
}

.hero__divider {
  margin: 0 0.1em;
}

.posts a {
  font-size: 1.6rem;
  color: #32414d;
  text-decoration: none;
  border-bottom: solid 0.2rem rgba(50, 65, 77, 0.5);
}

@media screen and (min-width: 480px) {
  .posts a {
    font-size: 1.6rem;
  }
}

.posts a:hover {
  text-decoration: none;
  border-bottom: solid 0.2rem #3867d6;
}

.posts__date {
  font-size: 1.3rem;
  color: #5b6771;
  margin-left: 0.4em;
}

@media screen and (min-width: 480px) {
  .posts__date {
    font-size: 1.3rem;
  }
}

.post__date {
  display: block;
  color: #5b6771;
  margin: 2em 0 1em;
}
